.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  width: 500px;
  height: auto;
  margin: 150px auto 0 auto;
  padding: 30px;
  border: 3px solid rgb(2, 120, 14);
  border-radius: 20px;
  background-color: rgb(174, 166, 148);
  color: black;
  text-align: center;
}
