.formWrapper {
  align-items: center;
  width: 300px;
  display: flex;
  flex-direction: column;
  gap: 20px;
  border: 1px black solid;
  padding: 20px;
  border: none;
  border-radius: 20px;
  background-color: #ede9061e;
  box-shadow: 5px 5px 9px -1px rgba(0, 0, 0, 0.47);
  -webkit-box-shadow: 5px 5px 9px -1px rgba(0, 0, 0, 0.47);
  -moz-box-shadow: 5px 5px 9px -1px rgba(0, 0, 0, 0.47);
  margin-bottom: 25px;
}

.label {
  display: flex;
  flex-direction: column;
  gap: 10px;
  font-size: 20px;
  font-weight: 700;
}

.input {
  border-radius: 5px;
  background-color: white;
  border: 2px solid #0539f4;
  padding: 5px;
  height: 20px;
}

.input:hover,
.input:focus {
  border: 2px solid #f7f706;
}

.buttonAdd {
  width: 110px;
  height: auto;
  cursor: pointer;
  font-weight: 700;
  border-radius: 15px;
  background-color: #0459f6;
  border-color: transparent;
  color: #000;
  padding: 10px;
  color: white;
  font-size: 15px;
  display: inline-block;
  position: relative;
  padding: 10px;
  box-shadow: 5px 5px 9px -1px rgba(0, 0, 0, 0.47);
  -webkit-box-shadow: 5px 5px 9px -1px rgba(0, 0, 0, 0.47);
  -moz-box-shadow: 5px 5px 9px -1px rgba(0, 0, 0, 0.47);
}

.buttonAdd:hover,
.buttonAdd:focus {
  cursor: pointer;
  border: 2px solid #0539f4;
  background-color: #f7f706;
  font-weight: 700;
  border-radius: 20px;
  color: #0b0b0b;
  position: relative;
  scale: 1.15;
}
